import React from 'react';
import Layout from '../components/layout';
import { graphql } from 'gatsby';
import styled from 'styled-components';
import { GatsbyImage, getImage } from 'gatsby-plugin-image';
import AboutGeneral from '../components/about_general';
import AboutLetter from '../components/about_letter';
import Leadership from '../components/leadership';

const AboutTemplate = ({ data }) => {
  const { html, frontmatter } = data.markdownRemark;
  const { about, aboutmatter } = data.markdownRemark;
  const profileImage = getImage(frontmatter.profile_image);

  return (
    <Layout title={frontmatter.title}>
      <div style={{ margin: "0px 10vw", paddingTop: "70px"}}>
        <br/>
        <br/>
        <br/>
        <h1 style={{ color: "#013b89", textAlign: "center" }}>About Us</h1>
        <br/>
        <h3>About Pre-Law Shadowers</h3>
        <br/>
        <p> Pre-Law Shadowers is a student-led organization dedicated to providing pre-law students across universities in Canada with virtual shadowing opportunities. Our goal is to help young students engage and learn more about the legal careers they plan to pursue. It can be difficult to find learning opportunities during the pandemic, so we started this organization in the hopes that it can be both accessible and educationally fulfilling for students.</p>
        <br/><br/>
        <p>We have created a great space where students can network with each other, thus creating lasting friendships and a welcoming community. They can also network with legal professionals in the field varying from lawyers, paralegals, law clerks, judges and even current law students, in order to learn more about the futures they aspire to build for themselves.</p>
        <br/><br/>
        <p>Through Pre-Law Shadowers, students can also gain insight and exposure into the legal field with exciting opportunities for volunteer programs and internships. Overall, this is an amazing platform that every pre-law student should be a part of!</p>
        <br/>
        <br/>
        <br/>
      </div>
      <AboutLetter/>
    </Layout>
  );
};

export default AboutTemplate;

const AboutWrapper = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 100%;
  background-color: #013b89;
  backgroundImage: '/media/3.jpeg';
  backgroundPosition: center;
  backgroundAttachment: fixed;

  @media screen and (max-width: 1000px) {
    & {
      flex-direction: column;
    }

    & > * {
      margin-top: 2rem;
      width: 100%;
      text-align: center;
    }
  }
`;

const AboutImageWrapper = styled(GatsbyImage)`
  display: block;
  border-radius: 50%;
  height: 300px;
  width: 300px;
  flex-grow: 1;
`;

const AboutCopy = styled.div`
margin: 50px 0px;
flex-grow: 2;
  max-width: 60ch;
  & h1 {
    color: #ffffff;

  }
  & h1 span {
    color: #ffda59;
  }
  & p {
    font-size: var(--size-400);
  }
`;

export const pageQuery = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
        profile_image {
          childImageSharp {
            gatsbyImageData(placeholder: BLURRED, formats: PNG, height: 400)
          }
        }
      }
    }
  }
`;
